body
{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;

    margin: 0;
    padding: 0;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code
{
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body
{
    max-width: 1100px;
    margin: 0 auto;
}
header
{
    text-align: center;
}

.titleBlock{
    width: 100%;
    height: 80px;
    
    margin-bottom: 10px;

    text-align: center;

    border: 1.2px double black;
    border-radius: 10px;
    background: -webkit-gradient(linear, right bottom, left top, from(#61dbfb), to(#25609C));
    background: -webkit-linear-gradient(bottom right, #61dbfb, #25609C);
    background: linear-gradient(to top left, #61dbfb, #25609C);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);
}
.DisplayBlock
{
    
    font-size: 12pt;

    overflow-y: auto;

    width: 100%;
    height: 400px;
    margin-bottom: 10px;

    border: 1.2px double black;
    border-radius: 10px;
    background: -webkit-gradient(linear, right bottom, left top, from(#61DBFB), to(#25609C));
    background: -webkit-linear-gradient(bottom right, #61DBFB, #25609C);
    background: linear-gradient(to top left, #61DBFB, #25609C);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);
}
p
{
padding: 10px 20px;
}

.ButtonBlock
{
    width: 100%;
    height: 70px;
    margin-bottom: 10px;

    text-align: center;

    border: 1.2px double black;
    border-radius: 10px;
    background: -webkit-gradient(linear, right bottom, left top, from(#61DBFB), to(#25609C));
    background: -webkit-linear-gradient(bottom right, #61DBFB, #25609C);
    background: linear-gradient(to top left, #61DBFB, #25609C);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);
}

button
{   
    width: 85px;
    height: 40px;
    margin-top: 15px;
    margin-right: 10px;
    margin-left: 10px;

    cursor: pointer;
    text-align: center;
    text-transform: uppercase;

    border-radius: 4px;
    outline: none;
    background: -webkit-gradient(linear, right bottom, left top, from(#f0dec8), to(#f09595));
    background: -webkit-linear-gradient(bottom right, #f0dec8, #f09595);
    background: linear-gradient(to top left, #f0dec8, #f09595);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);

    
}

.EditBlock{
  width: 100%;
  height: 350px;
  border: 1.2px double black;
  background: -webkit-gradient(linear, right bottom, left top, from(#61DBFB), to(#25609C));
  background: -webkit-linear-gradient(bottom right, #61DBFB, #25609C);
  background: linear-gradient(to top left, #61DBFB, #25609C);
  box-shadow: 0.4em 0.4em 5px rgba(122, 122, 122, 0.5);
  border-radius: 10px;
  text-align: center;
}
textarea{
    height: 300px;
    width: 95%;
    margin: 20px 0;
    border-radius: 10px;
    outline: none;
}
.StyleBlock
{
	display: block;
    font-size: 33px;

    width: 100%;
    height: 350px;
    margin-top: 10px;

    border: 1.2px double black;
    border-radius: 10px;
    background: -webkit-gradient(linear, right bottom, left top, from(#61DBFB), to(#25609C));
    background: -webkit-linear-gradient(bottom right, #61DBFB, #25609C);
    background: linear-gradient(to top left, #61DBFB, #25609C);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);
}
.StyleBlock form{
    margin-top: 10px;
    margin-left: 10px;
    padding-bottom: 10px;
}
.textSize{
    margin-right: 10px;
    margin-left: 10px;
}
form label select{
    font-size: 22px;
    margin-left: 10px;
    border-radius: 10px;
    outline: none;
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);
    border-radius: 5px 5px 5px 5px;
}
.StyleBlock form input{
    margin-left: 16px;
    margin-right: 20px;
    font-size: 25px;
}
.AddBlock
{

    width: 100%;
    height: 400px;
    margin-top: 10px;


    border: 1.2px double black;
    border-radius: 10px;
    background: -webkit-gradient(linear, right bottom, left top, from(#61DBFB), to(#25609C));
    background: -webkit-linear-gradient(bottom right, #61DBFB, #25609C);
    background: linear-gradient(to top left, #61DBFB, #25609C);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);


   
    font-size: 20px;
    margin-right: 40px;
    }
    
    .AddBlock h1{
        margin-left: 10px;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .AddBlock label{
        margin-left: 10px;
        font-size: 26px;
    }
    
    .AddBlock form,
    .AddBlock button{
        padding-left: 10px; 
    }
    div form{
        display: block;
        margin-top: 5px;
    }
  
    .tableForm label input,
    .tableForm label select{
        margin-right: 20px;
    }
    
    .tableForm button{
        margin-left: 21px;
        margin-top: 20px;
        display: block;
    }
    
    .tableForm label input{
        width: 45px;
        height: 20px;
        margin-left:12px;
    }
    
    .listForm h2{
        margin-top: 0;
        margin-bottom: 5px;
    }
    
    .listForm form{
        display: block;
        margin-top: 10px;
    }
    .listForm label input{
        width: 45px;
        height: 20px;
        margin-left:12px;
    }
    .listForm button{
        margin-top: 20px;
        margin-left: 18px;
    }

