.ButtonBlock
{
    width: 100%;
    height: 70px;
    margin-bottom: 10px;

    text-align: center;

    border: 1.2px double black;
    border-radius: 10px;
    background: linear-gradient(to top left, #61DBFB, #25609C);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);
}

button
{   
    width: 85px;
    height: 40px;
    margin-top: 15px;
    margin-right: 10px;
    margin-left: 10px;

    cursor: pointer;
    text-align: center;
    text-transform: uppercase;

    border-radius: 4px;
    outline: none;
    background: linear-gradient(to top left, #f0dec8, #f09595);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);

    
}
