.StyleBlock
{
	display: block;
    font-size: 33px;

    width: 100%;
    height: 350px;
    margin-top: 10px;

    border: 1.2px double black;
    border-radius: 10px;
    background: linear-gradient(to top left, #61DBFB, #25609C);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);
}
.StyleBlock form{
    margin-top: 10px;
    margin-left: 10px;
    padding-bottom: 10px;
}
.textSize{
    margin-right: 10px;
    margin-left: 10px;
}
form label select{
    font-size: 22px;
    margin-left: 10px;
    border-radius: 10px;
    outline: none;
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);
    border-radius: 5px 5px 5px 5px;
}
.StyleBlock form input{
    margin-left: 16px;
    margin-right: 20px;
    font-size: 25px;
}