.AddBlock
{

    width: 100%;
    height: 400px;
    margin-top: 10px;


    border: 1.2px double black;
    border-radius: 10px;
    background: linear-gradient(to top left, #61DBFB, #25609C);
    box-shadow: .4em .4em 5px rgba(122, 122, 122, .5);


   
    font-size: 20px;
    margin-right: 40px;
    }
    
    .AddBlock h1{
        margin-left: 10px;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .AddBlock label{
        margin-left: 10px;
        font-size: 26px;
    }
    
    .AddBlock form,
    .AddBlock button{
        padding-left: 10px; 
    }
    div form{
        display: block;
        margin-top: 5px;
    }
  
    .tableForm label input,
    .tableForm label select{
        margin-right: 20px;
    }
    
    .tableForm button{
        margin-left: 21px;
        margin-top: 20px;
        display: block;
    }
    
    .tableForm label input{
        width: 45px;
        height: 20px;
        margin-left:12px;
    }
    
    .listForm h2{
        margin-top: 0;
        margin-bottom: 5px;
    }
    
    .listForm form{
        display: block;
        margin-top: 10px;
    }
    .listForm label input{
        width: 45px;
        height: 20px;
        margin-left:12px;
    }
    .listForm button{
        margin-top: 20px;
        margin-left: 18px;
    }
